import { useEffect, useLayoutEffect } from "react";
import Wrap from "../Wrap";

import Main from "../Main";
import Movies from "../Movies";
import { Routes, Route, useLocation, useNavigate } from "react-router-dom";
import { Login, Register } from "../Auth";

import SavedMovies from "../SavedMovies";
import Profile from "../Profile/Profile";
import NotFoundPage from "../NotFoundPage";
import InfoToolTip from "../Modal/InfoToolTip";
import Modal from "../Modal";

import ProtectedRoute from "../ProtectedRoute";

import { useStore } from "../../services/StoreProvider";
import { getUser } from "../../services/actions/user";
import { CLOSE_TOOL_TIP } from "../../services/actions/toolTip";
import { getSavedMovies } from "../../services/actions/savedMovies";
import { SET_STATE_MAIN_MOVIES } from "../../services/actions/mainMovies";
import { mainPath } from "../../utils/constants";

function App() {
  const [state, dispatch] = useStore();
  const { loggedIn } = state;
  const location = useLocation();
  const navigate = useNavigate();

  function checkDataInStorage() {
    const moviesLocalState = JSON.parse(localStorage.getItem("moviesLocalState"));
    if (moviesLocalState) {
      dispatch({ type: SET_STATE_MAIN_MOVIES, mainMovie: moviesLocalState });
    }
  }

  useLayoutEffect(() => {
    getUser(dispatch).then((success) => {
      if (success) {
        navigate(location.pathname);
      }
    });
    if (loggedIn) {
      checkDataInStorage();
    }
  }, [loggedIn]);

  useEffect(() => {
    if (loggedIn) {
      getSavedMovies(dispatch);
    }
  }, [dispatch, loggedIn]);

  useEffect(() => {
    if (state.toolTip.isOpen) {
      setTimeout(() => {
        dispatch({ type: CLOSE_TOOL_TIP });
      }, 5000);
    }
  }, [dispatch, state.toolTip.isOpen]);

  return (
    <div className="page">
      <Routes>
        <Route
          exact
          path={mainPath}
          element={
            <Wrap>
              <Main />
            </Wrap>
          }
        />
        <Route
          path={mainPath + "/movies"}
          element={
            <ProtectedRoute path={mainPath + "/movies"}>
              <Movies />
            </ProtectedRoute>
          }
        />
        <Route
          path={mainPath + "/saved-movies"}
          element={
            <ProtectedRoute path={mainPath + "/saved-movies"}>
              <SavedMovies />
            </ProtectedRoute>
          }
        />
        <Route
          path={mainPath + "/profile"}
          element={
            <ProtectedRoute path={mainPath + "/profile"}>
              <Profile />
            </ProtectedRoute>
          }
        />
        <Route path={mainPath + "/sign-in"} element={<Login />} />
        <Route path={mainPath + "/sign-up"} element={<Register />} />
        <Route path="*" element={<NotFoundPage />} />
      </Routes>
      {state.toolTip.isOpen && (
        <Modal>
          <InfoToolTip />
        </Modal>
      )}
    </div>
  );
}

export default App;
